<template>
	<div class="swiper-container" :class='swipeid'>
		<div class="swiper-wrapper">
			<slot name='swiper-con'></slot>
		</div>
	</div>
</template>

<script>
	//引入swiper轮播插件js文件
	import '../assets/swiper.min.js'         
	export default {
		props:{
			swipeid:{   //定义swiper容器类名
				type:String,
				default:''
			},
			effect:{    //轮播图切换效果
				type:String,
				default:'slide'
			},
			loop:{		//循环
				type:Boolean,
				default:true
			},
			direction:{  //播放方向
				type:String,
				default:'horizontal'    //水平
			},
			autoplay:{   //自动播放
				type:Number,
				default:5000
			}
		},
		mounted:function(){
				var that = this;
				new Swiper('.'+that.swipeid,{
					loop:that.loop,
					pagination:'.swiper-pagination',
					paginationType:that.paginationType,
					autoplay:that.autoplay,
					direction:that.direction,
					effect:that.effect,
					//用户操作swiper后，是否autoplay
					autoplayDisableOnInteraction:false
				});
			}
	}
</script>

<style>
	/*引入swiper轮播插件css文件,分号必须写*/
	@import '../assets/swiper.min.css';
	.swiper-container img{width: 100%;}
</style>


