<template>
	<div class="cateWrap">
		<h3 class="newpage"><span @click='fn()'><</span>厨房妈妈</h3>
		<div class="cont">
			<h3>五种果汁喝出美丽</h3>
			<div class="time">
				<time>2015-5-30</time>
				<span><i><img src="../../assets/images/icon2.jpg"></i>浏览174</span>
			</div>
			<p>每个季节都有不同的时令水果，尤其是夏季，堪比水果盛宴。有的懒人懒得吃水果就靠果汁补充营养，你知道自己需要补充什么营养吗？吐血推荐5款让你喝出健康的果汁。</p>
			<p>排毒排便-香蕉牛奶汁</p>
			<p>适当加入牛奶调理，可以补充更多钙。对于正在加肥中的女孩来说，也比较有饱足感。经常失眠或有痛经的女孩也可以喝喝看。</p><br/><br/>
			<p>止咳防晕-芒果汁</p>
			<p>退火利尿-椰汁</p>
			<p>不过有的人会怕椰子的味道，因为椰汁生冷性寒，女孩们如果想喝椰汁来消暑，或是肠胃不好的人，喝之前要三思。</p>
			<p>水果之王-奇异果汁</p>
		</div>
	</div>
</template>

<script>
	export default {
		methods:{
			fn:function(){
				this.$router.push('/foods')
			}
		}
	}
</script>

<style>
	.cateWrap{padding: 0 0.2rem;}
	.cateWrap .newpage{color: #666666;height: 0.5rem;line-height: 0.5rem;background: #fff;text-align: center;padding-top: 0.1rem;}
	.cateWrap .newpage span{float: left;font-size: 0.4rem;}
	.cateWrap .cont{background: #FAFAFA;}
	.cateWrap .cont h3{height: 0.4rem;line-height: 0.4rem;}
	.cateWrap .cont .time{height: 0.25rem;}
	.cateWrap .cont time{float: left;}
	.cateWrap .cont span{float: right;color: #999999;vertical-align: top;}
	.cateWrap .cont p{line-height:0.4rem;color: #333;text-indent: 2em;}

</style>


